import React, { Component } from 'react';
import LineChart from './graphs/line';
import BarChart from './graphs/bar';
import AreaChart from './graphs/area';
import './styles.css';

class Tile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      edit: false
    }
  }
  render() {
    const {i, type} = this.props;
    return (
      <div>
        <div className='tile-title'>test title</div>
        <div>
          {
            (() => {
                return {
                  line: <LineChart />,
                  bar: <BarChart />,
                  area: <AreaChart />,
                  none: <div>none</div>
                }[type]
            })()
          }
        </div>
      </div>
    );
  }
}

export default Tile;
